*, *::before, *::after,
*:before, *:after {
  box-sizing: border-box; }

body {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300; }

h1 {
  font-weight: 400;
  text-align: center; }

.section {
  margin-top: 6rem;
  width: 100%; }

.form-container {
  width: 100%;
  max-width: 1040px;
  margin: auto; }
  .form-container fieldset {
    border: none;
    padding: 0; }
  .form-container input[type="text"] {
    display: block;
    width: 100%;
    margin-bottom: 2.5rem;
    height: 2.5rem;
    padding: .7rem;
    border: 1px solid #d3d2d2;
    border-radius: 5px; }

.btn-container {
  width: 80%;
  text-align: center;
  margin: auto; }

.btn {
  padding: .8rem 1.8rem;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  margin: 1rem 0; }
  .btn.btn__blue {
    background-color: #1c69a8;
    color: #ffffff; }
